//  or 
export function getThesisType(grade:string) {
  switch (grade) {
    case 'B4':
      return ''
    case 'M2':
      return ''
    case 'D3':
      return ''
    default:
      return 'unknown'
  }
}

export function getLink(link?:string, kind?:string) {
  if (link == undefined) {
    return undefined
  }
  else if (kind == 'staff'){
    return `member/staff/${link}`
  }

  else {
    return `member/current/${link}`
  }
}